.container-skills {
    padding: 0 10%;
}

.container-skills .section-title {
    font-size: 80px;
    color: #004737;
    margin: 0 auto;
    padding: 60px 0;
    text-align: center;
}

.container-skills .container-skills-body {
    display: flex;
    justify-content: center;
    flex-wrap: wrap;
}

.container-skills .container-skills-body .section-body {
    display: inline-block;
    position: relative;
    min-width: 15%;
    margin: 20px 20px;
    height: 8rem;
}

.container-skills .container-skills-body .section-body .container-skill-logo-bg {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    background: #FAF2D5;
    border-radius: 20px;
    transition: all 0.2s ease-in-out;
}

.container-skills .container-skills-body .section-body:hover .container-skill-logo-bg {
    transform: scale(0.9, 0.9);
}

.container-skills .container-skills-body .section-body .logo {
    position: absolute;
    height: 60px;
    top: 37%;
    left: 50%;
    transform: translateX(-50%) translateY(-50%);
    transition: all 0.2s ease-in-out;
}

.container-skills .container-skills-body .section-body:hover .logo {
    transform: scale(1.1, 1.1) translateX(-45%) translateY(-45%);
}

.container-skills .container-skills-body .section-body .text {
    position: absolute;
    text-align: center;
    width: 100%;
    font-size: 23px;
    font-weight: 500;
    color: #004737;
    top: 66%;
    /*transform: translateY(-50%);*/
}